export const useUserInfo = () => {


    const {contrcts, blockNumber, listToken, web3} = useWeb3();
    const {pid, tokenNick, address, lpAddr, lpId, sunderToEthPrice: sunderPrice} = useStakeContext();

    const {account} = useWallet();
    const [balance, setBalance] = useState<any>({
        amount: new BigNumber(0),
        rewardDebt: new BigNumber(0),
        deposits: new BigNumber(0),
        apy: null
    });

    const lpContrcts = useMemo(() => {
        return new web3.eth.Contract(lpAbi.abi, lpAddr);
    }, [])

    const apyCalculate = useCallback(async (price) => {
        if (!price) return null
        let {allocPoint, amount} = await contrcts.chefContract.methods.poolInfos(pid).call();
        if (amount == 0) {
            return null;
        }
        let totalAllocPoint = await contrcts.chefContract.methods.totalAllocPoint().call();
        let totalSupply = await lpContrcts.methods.totalSupply().call();
        let token1: string = await lpContrcts.methods.token1().call();
        let reward = await contrcts.chefContract.methods.reward().call();
        let period = await contrcts.chefContract.methods.period().call();
        const {
            _reserve0,
            _reserve1,
        } = await lpContrcts.methods.getReserves().call();

        let rewardInUsd = new BigNumber(reward).times(allocPoint / totalAllocPoint)
            .div(period)
            .times(365 * 24 * 60 * 60)
            .times(price);

        let daiAmount;
        if (token1.toLowerCase() === address.toLowerCase()) {
            daiAmount = _reserve0;
        } else {
            daiAmount = _reserve1;
        }


        // @ts-ignore
        const otherTokenPrice = listToken[lpId]["usd"];
        let poolInUsd = new BigNumber(amount).times(daiAmount * otherTokenPrice).times(2).div(totalSupply);

        const apy = rewardInUsd.div(poolInUsd).times(100);


        return apy;
    }, [contrcts, account, pid, lpContrcts, listToken])

    const facethBack = useCallback(async () => {
        let _balance = await contrcts.chefContract.methods.userInfos(pid, account).call();
        let _pendingSushi = await contrcts.chefContract.methods.pendingToken(pid, account).call();
        let apy = await apyCalculate(sunderPrice);
        setBalance({
            amount: new BigNumber(_balance['amount']),
            rewardDebt: new BigNumber(_balance['rewardDebt']),
            deposits: new BigNumber(_pendingSushi[0]),
            apy,
        });
    }, [contrcts, account, pid, listToken, sunderPrice]);

    useEffect(() => {
        if (!sunderPrice) return;
        facethBack();
    }, [
        blockNumber,
        sunderPrice
    ])

    return balance;
}

export const useSunderPrice = (): string | null => {

    const {contrcts, listToken, blockNumber} = useWeb3();
    const [ethtoSunderPrice, setEthtoSunderPrice] = useState<string | null>(null);

    const cb = useCallback(async () => {
        if (!contrcts.ethSwapSunderContract || !listToken['ethereum']) return null;
        // @ts-ignore
        const {ethereum: {usd: ethPrice}, "sunder-goverance-token": {usd: sunderPrice}} = listToken;
        const {
            _reserve0: sudnerAmount,
            _reserve1: ethAmount
        } = await contrcts.ethSwapSunderContract.methods.getReserves().call();


        setEthtoSunderPrice(sunderPrice);
    }, [listToken, contrcts]);

    useEffect(() => {
        cb();
    }, [
        blockNumber
    ])

    return ethtoSunderPrice
}


export const useStep = () => {
    const {contrcts, blockNumber} = useWeb3();
    const {account} = useWallet();
    const [stepData, setStepData] = useState({
        epochId: '0',
        reward: '0',
        startTime: '0',
        endTime: '0',
    });
    const facethBack = useCallback(async () => {
        let reward = await contrcts.chefContract.methods.reward().call();
        let epochId = await contrcts.chefContract.methods.epochId().call();
        let startTime = await contrcts.chefContract.methods.startTime().call();
        let endTime = await contrcts.chefContract.methods.endTime().call();
        setStepData({
            epochId,
            reward,
            startTime,
            endTime,
        });
    }, [contrcts, account]);

    useEffect(() => {
        facethBack();
    }, [
        blockNumber
    ])

    return stepData;
}
