import {useWeb3} from "../provider/web3_provider";
import {useSendFn} from "../utils/web3_send";
import {useCallback, useEffect, useState} from "react";
import BigNumber from "bignumber.js";


export const useDeposit = () => {
    const {contrcts, web3} = useWeb3();
    const handleSendFn = useSendFn(contrcts.stakeAddress);
    const depositAll = useCallback(async () => {
        let method = contrcts.stakeContract.methods.depositAll();
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3
    ])

    const deposit = useCallback(async (amount: string) => {
        let method = contrcts.stakeContract.methods.deposit(amount);
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3
    ]);

    return {depositAll, deposit}
}


export const useWithdraw = () => {
    const {contrcts, web3} = useWeb3();
    const handleSendFn = useSendFn(contrcts.stakeAddress);
    const withdrawAll = useCallback(async () => {
        let method = contrcts.stakeContract.methods.withdrawAll();
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3
    ])

    const withdraw = useCallback(async (amount: string) => {
        let method = contrcts.stakeContract.methods.withdraw(amount);
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3

    ]);

    return {withdraw, withdrawAll}
}

export const usePricePerFullShare = () => {
    const {contrcts, blockNumber} = useWeb3();
    const [allowance, setAllowance] = useState(new BigNumber(0));

    let cb = useCallback(async () => {
        let method = await contrcts.stakeContract.methods.getPricePerFullShare().call();
        setAllowance(method)
    }, []);

    useEffect(() => {
        cb()
    }, [blockNumber])


    return allowance
}
